import React, { FunctionComponent } from "react"
import Layout from "@nehalist/gatsby-theme-nehalem/src/components/layout"
import SEO from "@nehalist/gatsby-theme-nehalem/src/components/seo"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import { Row, Col } from "antd"
import { LinkOutlined } from "@ant-design/icons"
import styled from "styled-components"

const Common = styled.div`
  background: linear-gradient(-45deg, #7d96ae, #40556a) repeat scroll 0 0
    transparent;
  color: white;
  min-height: calc(100vh - 70px);
  font-size: 16px;
  @media (max-width: 576px) {
    font-size: 12px;
  }
`

const Title = styled.h1`
  color: white;
`

const LinkText = styled.span`
  margin-left: 16px;
`

const Content = styled.div`
  font-size: 1.5em;
  line-height: 2em;
  text-shadow: 0 2px 2px rgb(0 0 0 / 15%);
`

const ContentDes = styled.p`
  margin: 50px 0;
  opacity: 0.9;
`

const ButtonLink = styled.a`
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 20px;
  margin-top: 20px;
  color: #fff;
  min-width: 210px;
  transition: all 0.25s ease-in-out;
  &:hover {
    background-color: #fff;
    color: #000;
    cursor: pointer;
  }
`

const links = [
  {
    title: "就学45分钟的gopher",
    desc: "积水成渊，蛟龙生焉知",
    link: "https://www.cnblogs.com/yahuian",
  },
]

const CustomPage: FunctionComponent<{ location: Location; data: any }> = ({
  data,
  location,
}) => {
  return (
    <Layout bigHeader={false}>
      <SEO
        location={location}
        title={`pipihua的友链`}
        description="各路大佬的链接"
      />
      <Common>
        {links.map(item => (
          <Row
            key={item.link}
            justify="center"
            align="middle"
            style={{ padding: "50px 0" }}
            gutter={[16, 30]}
          >
            <Col xs={20} sm={10}>
              <Content>
                <Title>{item.title}</Title>
                <ContentDes>{item.desc}</ContentDes>
                <ButtonLink href={item.link} target="_blank">
                  <LinkOutlined />
                  <LinkText>博客链接</LinkText>
                </ButtonLink>
              </Content>
            </Col>
            <Col xs={20} sm={6}>
              <Img
                imgStyle={{
                  borderRadius: 150,
                }}
                fixed={data.file.childImageSharp.fixed}
                alt="gopher-anyh"
              />
            </Col>
          </Row>
        ))}
      </Common>
    </Layout>
  )
}

export default CustomPage

export const query = graphql`
  query {
    file(sourceInstanceName: { eq: "themeAssets" }, name: { eq: "anyh" }) {
      childImageSharp {
        fixed(width: 300, height: 300) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`
